import React from 'react';
import { Row, Col } from 'antd';
// import the core library.
import ReactEchartsCore from 'echarts-for-react/lib/core';

// then import echarts modules those you have used manually.
import echarts from 'echarts/lib/echarts';
import 'echarts/lib/chart/line';
import 'echarts/lib/chart/bar';
import 'echarts/lib/chart/pie';
// import 'echarts/lib/chart/scatter';
// import 'echarts/lib/chart/radar';

// import 'echarts/lib/chart/map';
// import 'echarts/lib/chart/treemap';
// import 'echarts/lib/chart/graph';
// import 'echarts/lib/chart/gauge';
// import 'echarts/lib/chart/funnel';
// import 'echarts/lib/chart/parallel';
// import 'echarts/lib/chart/sankey';
// import 'echarts/lib/chart/boxplot';
// import 'echarts/lib/chart/candlestick';
// import 'echarts/lib/chart/effectScatter';
// import 'echarts/lib/chart/lines';
// import 'echarts/lib/chart/heatmap';

// import 'echarts/lib/component/graphic';
// import 'echarts/lib/component/grid';
// import 'echarts/lib/component/legend';
import 'echarts/lib/component/tooltip';
// import 'echarts/lib/component/polar';
// import 'echarts/lib/component/geo';
// import 'echarts/lib/component/parallel';
// import 'echarts/lib/component/singleAxis';
// import 'echarts/lib/component/brush';

import 'echarts/lib/component/title';

// import 'echarts/lib/component/dataZoom';
// import 'echarts/lib/component/visualMap';

// import 'echarts/lib/component/markPoint';
// import 'echarts/lib/component/markLine';
// import 'echarts/lib/component/markArea';

// import 'echarts/lib/component/timeline';
// import 'echarts/lib/component/toolbox';

// import 'zrender/lib/vml/vml';

function ChartView(props) {
  return (
    <Row>
      <Col span={12}>
        <ReactEchartsCore
          echarts={echarts}
          option={{
            title: {
              text: '',
              left: 'center',
              textStyle: {
                left: 'center',
                top: 10,
                color: '#333'
              }
            },
            tooltip : {
              trigger: 'item',
              formatter: '{b}:{c}({d}%)'
            },
            series: [
              {
                name: '',
                type: 'pie',
                radius: '60%',
                label: {
                  formatter: '{b}:{c}({d}%)'
                },
                data: props.cityOpenData
              }
            ]
          }}
        />
      </Col>
      <Col span={12}>
        <ReactEchartsCore
          echarts={echarts}
          option={{
            title: {
              text: '',
              left: 'center',
              textStyle: {
                color: '#000000'
              }
            },
            tooltip : {
              trigger: 'item',
              formatter: '{b}:{c}({d}%)'
            },
            series: [
              {
                name: '',
                type: 'pie',
                radius: '60%',
                label: {
                  formatter: '{b}:{c}({d}%)'
                },
                data: props.secondCategoryOpenData
              }
            ]
          }}
        />
      </Col>
      <Col span={12}>
        <ReactEchartsCore
          echarts={echarts}
          option={{
            title: {
              text: '',
              left: 'center',
              textStyle: {
                color: '#000000'
              }
            },
            tooltip : {
              trigger: 'item',
              formatter: '{b}:{c}({d}%)'
            },
            series: [
              {
                name: '',
                type: 'pie',
                radius: '60%',
                label: {
                  formatter: '{b}:{c}({d}%)'
                },
                data: props.cityNewAddedAcceptData
              }
            ]
          }}
        />
      </Col>
      <Col span={12}>
        <ReactEchartsCore
          echarts={echarts}
          option={{
            title: {
              text: '',
              left: 'center',
              textStyle: {
                color: '#000000'
              }
            },
            tooltip : {
              trigger: 'item',
              formatter: '{b}:{c}({d}%)'
            },
            series: [
              {
                name: '',
                type: 'pie',
                radius: '60%',
                label: {
                  formatter: '{b}:{c}({d}%)'
                },
                data: props.secondCategoryNewAddedAcceptData
              }
            ]
          }}
        />
      </Col>
      <Col span={24}>
        <ReactEchartsCore
          echarts={echarts}
          option={{
            title: {
              text: '',
              left: 'center',
              textStyle: {
                color: '#000000'
              }
            },
            tooltip: {
              trigger: 'axis',
              axisPointer: {
                type: 'cross',
                crossStyle: {
                    color: '#999'
                }
              }
            },
            toolbox: {
              feature: {
                dataView: {show: true, readOnly: false},
                magicType: {show: true, type: ['line', 'bar']},
                restore: {show: true},
                saveAsImage: {show: true}
              }
            },
            xAxis: [
              {
                type: 'category',
                data: props.cities,
                axisPointer: {
                  type: 'shadow'
                }
              },
            ],
            yAxis: [
              {
                type: 'value',
                name: ''
              },
              {
                type: 'value',
                name: '',
                min: 0,
                max: 1
              }
            ],
            series: [
              {
                name: '',
                type: 'bar',
                barMaxWidth: 20,
                data: props.cityAcceptTotalData
              },
              {
                name: '',
                type: 'line',
                yAxisIndex: 1,
                data: props.cityCloseRateData
              }
            ]
          }}
        />
      </Col>
      <Col span={24}>
        <ReactEchartsCore
          echarts={echarts}
          option={{
            title: {
              text: '',
              left: 'center',
              textStyle: {
                color: '#000000'
              }
            },
            tooltip: {
              trigger: 'axis',
              axisPointer: {
                type: 'cross',
                crossStyle: {
                    color: '#999'
                }
              }
            },
            toolbox: {
              feature: {
                dataView: {show: true, readOnly: false},
                magicType: {show: true, type: ['line', 'bar']},
                restore: {show: true},
                saveAsImage: {show: true}
              }
            },
            xAxis: [
              {
                type: 'category',
                data: props.cities,
                axisPointer: {
                  type: 'shadow'
                }
              },
            ],
            yAxis: [
              {
                type: 'value',
                name: ''
              },
            ],
            series: [
              {
                name: '',
                type: 'bar',
                barMaxWidth: 20,
                data: props.cityCloseAverageHandleTimeData
              }
            ]
          }}
        />
      </Col>
      <Col span={24}>
        <ReactEchartsCore
          echarts={echarts}
          option={{
            title: {
              text: '',
              left: 'center',
              textStyle: {
                color: '#000000'
              }
            },
            tooltip: {
              trigger: 'axis',
              axisPointer: {
                type: 'cross',
                crossStyle: {
                    color: '#999'
                }
              }
            },
            toolbox: {
              feature: {
                dataView: {show: true, readOnly: false},
                magicType: {show: true, type: ['line', 'bar']},
                restore: {show: true},
                saveAsImage: {show: true}
              }
            },
            xAxis: [
              {
                type: 'category',
                data: props.cities,
                axisPointer: {
                  type: 'shadow'
                }
              },
            ],
            yAxis: [
              {
                type: 'value',
                name: ''
              },
            ],
            series: [
              {
                name: '',
                type: 'bar',
                barMaxWidth: 20,
                data: props.cityOpenAverageHandleTimeData
              }
            ]
          }}
        />
      </Col>
    </Row>
  )
}

export default ChartView;
